.hamburger_body {
  position: relative;
}

.hamburger_menu {
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 20px;
  left: 20px;
}

.mobile_nav {
  position: fixed;
  top: 0;
  right: -250px;
  width: 250px;
  height: 100%;
  background: #333;
  color: white;
  transition: right 0.3s ease-in-out;
  padding-top: 50px;
}

.mobile_nav.open {
  right: 0;
}

.mobile_nav ul {
  list-style: none;
  padding: 0;
  text-align: left;
}

.mobile_nav ul li {
  padding: 10px;
  border-bottom: 1px solid #444;
}

.mobile_nav a {
  color: white;
  text-decoration: none;
}

.close_menu {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 24px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}
